@import "blocks/fonts";
@import "blocks/reset";
@import "blocks/tools";
@import "blocks/global";

.page {
	position: relative;
	width: 1440px;
	margin: 0 auto;
	background-color: #F7F6F5;
}

.container {
	padding: 0 50px;
}

.header {
	margin: 50px 0;
}

.header__nav {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	column-gap: 20px;
}

.logo {
	justify-self: start;
	align-self: center;
}

.site-navigation {
	grid-column: 2 / 4;
	align-self: center;
	display: flex;
	justify-content: center;
}

.site-navigation__link {
	padding: 0 12px;
	font-weight: 500;
	font-size: 16px;
	line-height: 130%;
}

.user-navigation {
	justify-self: end;
	align-self: center;
}

.user-navigation__link {
	padding-left: 25px;
}

.main__grid {
	display: grid;
	grid-template-rows: repeat(3, auto);
	column-gap: 20px;
}

.page-title {
	font-weight: bold;
	font-size: 48px;
	line-height: 130%;
	margin-bottom: 70px;
	letter-spacing: 1px;
}

.filters-form {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 37px;
	margin-bottom: 60px;
}

.filters-form label {
	grid-row: 1 / 2;
}

select {
	border: none;
	border-bottom: 1px solid rgba(33, 30, 29, 0.5);
	background-color: #F7F6F5;
	padding-bottom: 10px;
	font-family: Manrope;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 130%;
	color: #211E1D;
	opacity: 0.5;
}


.models__list {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(3, auto);
	column-gap: 20px;
	row-gap: 50px;
	order: reverse;
}

.models__item img {
	width: 100%;
	display: block;
}

.models__link {
	display: block;
}

.models__text {
	display: grid;
	grid-template-rows: repeat(2, 1fr);
	grid-template-columns: repeat(2, auto);
	grid-auto-flow: column;
	position: relative;
	margin-top: 18px;
}

.models__title {
	align-self: center;
}

.models__price {
	align-self: center;
	padding-top: 7px;
}

.models__favourite {
	background-color: transparent;
	border: none;
	width: 24px;
	height: 24px;
	align-self: center;
	justify-self: end;

	&::after {
		content: "";
		display: block;
		width: 24px;
		height: 24px;
		background-image: url(../img/svg/favourite.svg);
		background-repeat: no-repeat;
		background-position: 0 0;
		background-size: cover;
	}
}

.more {
	margin: 80px auto 100px;
	display: grid;
}

.footer {
	padding: 70px 0 55px;
	background: #DEDBD9;
}

.more__btn {
	width: 324px;
	height: 76px;
	background-color: transparent;
	border: 1px solid #211E1D;
	justify-self: center;
	font-weight: 500;
	font-size: 16px;
}

.footer__grid {
	display: grid;
	grid-template-columns: 386px 299px 305px 350px;
}

.about {
	font-size: 14px;
	line-height: 130%;
}

.about__text {
	padding-top: 15px;
}

.contacts {
	display: flex;
	flex-direction: column;
}

.contacts__telephone {
	padding-bottom: 20px;
}

.contacts__email {
	padding-bottom: 20px;
}

.network {
	display: flex;
}

.network__item {
	padding-right: 19px;
}

.facebook {
	&::after {
		content: "";
		display: block;
		width: 20px;
		height: 20px;
		background-image: url(../img/svg/facebook.svg);
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.twitter {
	&::after {
		content: "";
		display: block;
		width: 20px;
		height: 20px;
		background-image: url(../img/svg/twitter.svg);
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.instagram {
	&::after {
		content: "";
		display: block;
		width: 20px;
		height: 20px;
		background-image: url(../img/svg/instagram.svg);
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.pinterest {
	&::after {
		content: "";
		display: block;
		width: 20px;
		height: 20px;
		background-image: url(../img/svg/pinterest.svg);
		background-repeat: no-repeat;
		background-size: cover;
	}
}

.services {
	list-style-type: none;
}

.services__item {
	padding-bottom: 20px;
}

.subscribe {
	position: relative;
}

input {
	width: 350px;
	padding: 47px 0 10px;
	border: none;
	border-bottom: 1px solid #211E1D;
	background-color: #DEDBD9;
}

input[placeholder] {
	opacity: 0.3;
}

.subscribe__btn {
	border: none;
	background-color: #DEDBD9;
	width: 26px;
	height: 12px;
	position: absolute;
	top: 67px;
	right: 0;
}
