@import "blocks/variables";
/* global -------------------------- */

html {
	height: 100%;
}

body {
	height: 100%;
	font-family: Manrope, Arial, Helvetica, sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 16px;
	line-height: 130%;
	color: #211E1D;
}


a {
	cursor: pointer;
	text-decoration: none;
	color: #211E1D;

}

// :link — ссылка, еще не посещенная пользователем;
a:link {}

//:hover — ссылка, на которую наведен курсор;
a:hover {}

//:current - ссылка на страницу, где находимся
a:current {}

//:active — активная ссылка (та, по которой совершается клик, или на которой удерживается кнопка мыши);
a:active {}

//:visited — посещенная ссылка.
img {
	max-width: 100%;
	height: auto;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}
