/* fonts -------------------------- */

/* manrope-regular - latin_cyrillic */
@font-face {
	font-family: 'Manrope';
	font-style: normal;
	font-weight: 400;
	src: url('../fonts/manrope-v8-latin_cyrillic-regular.eot');
	/* IE9 Compat Modes */
	src: local(''),
		url('../fonts/manrope-v8-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'),
		/* IE6-IE8 */
		url('../fonts/manrope-v8-latin_cyrillic-regular.woff2') format('woff2'),
		/* Super Modern Browsers */
		url('../fonts/manrope-v8-latin_cyrillic-regular.woff') format('woff'),
		/* Modern Browsers */
		url('../fonts/manrope-v8-latin_cyrillic-regular.ttf') format('truetype'),
		/* Safari, Android, iOS */
		url('../fonts/manrope-v8-latin_cyrillic-regular.svg#Manrope') format('svg');
	/* Legacy iOS */
}

